form .form-input {
  width: 100%;
  padding: 0.9rem;
  border-radius: 20px;
  color: black;
  background-color: white;
  margin: 1rem 0;
  font-size: 1.2rem;
  border: 3px solid black;
  box-shadow: 3px 3px 10px 6px black;

}

.flex-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.flex-column {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}


.flex-wrap {
  flex-wrap: wrap;
}

.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.justify-space-between {
  justify-content: space-between;
}

.align-center {
  align-items: center;
}

.bg-dark.text-white.p-2.text-center {
  font-size: 5rem;
  font-family: 'Passions Conflict';
  
}

.btn {
  box-shadow: 2px 2px 5px 3px black;
  background-color: black;
  font-family: 'Passions Conflict';
  font-size: 3rem;
  border: none;
  color: white;
  font-weight: 100;
  padding: 10px;
  border-radius: 30px;
  text-decoration: none;
  display: inline-block;
  transition: all 0.3s ease 0s;
}

.btn:hover {
  color: #59d0ff;
  font-weight: 700;
  letter-spacing: 3px;
  background: none;
  -webkit-box-shadow: 0px 5px 40px -10px #000000;
  -moz-box-shadow: 0px 5px 40px -10px #000000;
  transition: all 0.3s ease 0s;
}

h2 {
  color: white;
  text-shadow: 2px 2px black;
}

.errorMessage {
  font-style: oblique;
  padding: 15px;
  color: #59d0ff;
}


















.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#newJournalEntryForm {
    background: #0d0e0f;
    box-shadow: 6px 6px 10px 12px black;
    padding: 10px;
    border-radius: 20px;
    color: white;
    font-family: 'Ubuntu';
    font-size: 1.5rem;
    width: 80%;
    margin-top: 80px;
    margin-bottom: 50px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

#newJournalEntryForm label {
    font-family: 'Passions Conflict';
    font-size: 3.0rem;
}



#submitJournalButton {
    font-family: 'Passions Conflict';
    font-size: 3rem;
    border: none;
    color: #17191a;
    font-weight: 100;
    padding: 10px;
    border-radius: 30px;
    text-decoration: none;
    display: inline-block;
    transition: all 0.3s ease 0s;
  }
  
#submitJournalButton:hover {
    color: #59d0ff;
    font-weight: 700;
    letter-spacing: 3px;
    background: none;
    -webkit-box-shadow: 0px 5px 40px -10px #000000;
    -moz-box-shadow: 0px 5px 40px -10px #000000;
    transition: all 0.3s ease 0s;
  }

#newJournalEntryForm textarea {
    border: none;
    overflow: auto;
    outline: none;
    box-shadow: none;

    resize: none; /*remove the resize handle on the bottom right*/

    background: linear-gradient(to right, #cacaca, #59d0ff 80%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    width: 100%;
    height: 500px;
    font-size: 3rem;
    font-family: 'Passions Conflict';
}

#journalImageLink {
    border-style: solid;
    border-width: 1px;
    border-radius: 20px;
    border-color: white;

    margin: 10px;

    font-size: 2rem;
    background: linear-gradient(to right, #cacaca, #59d0ff 80%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Passions Conflict';
}

#loginError {
    font-family: 'Passions Conflict';
    font-size: 3rem;
    color: white;
    margin: 30px;
}
.homeLink {
    color: #59d0ff;
}
.homeLink:active {
    color: white;
    font-weight: 900;
}
.myJournalsHeader {
